  right: 8px;
}

.popup-close:before, .popup-close:after {
  content: "";
  width: 14px;
  height: 3px;
  background-color: #8f9cb5;
  position: absolute;
  top: 12px;
}

.popup-close:before {
  left: 8px;
  transform: rotate(45deg);
}

.popup-close:after {
  right: 8px;
  transform: rotate(-45deg);
}

.popup-close:hover:before, .popup-close:hover:after {
  background-color: #35a785;
  transition: all .3s;
}

